<template>
  <Input v-model="data.shopify_private_app_api_key" :view="validation.shopify_private_app_api_key.$invalid ? 'danger' : 'default'" class="my-4" label="Shopify Private API Key" placeholder="" block />
  <small class="text-red-base block mb-4" v-for="(error, errorIndex) of validation.shopify_private_app_api_key.$errors" :key="errorIndex">{{ error.$message }}. </small>
  <Input v-model="data.shopify_private_app_passsword" :view="validation.shopify_private_app_passsword.$invalid ? 'danger' : 'default'" class="my-4" label="Shopify Private App Password" placeholder="" block />
  <small class="text-red-base block mb-4" v-for="(error, errorIndex) of validation.shopify_private_app_passsword.$errors" :key="errorIndex">{{ error.$message }}. </small>
  <Input v-model="data.shopify_base_url" :view="validation.shopify_base_url.$invalid ? 'danger' : 'default'" class="my-4" label="Shopify Base URL" placeholder="" block />
  <small class="text-red-base block mb-4" v-for="(error, errorIndex) of validation.shopify_base_url.$errors" :key="errorIndex">{{ error.$message }}. </small>


</template>

<script>
import { defineComponent } from 'vue'
import { useShowAll } from 'composable/integration'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Input from 'bbUi/components/input/input.vue'
import { ElMessage } from 'element-plus'
import { isFunction } from '@/utils/is'

export default defineComponent({
  components: {
    Button,
    Alert,
    Input
  },
  async setup(){
    const { state, data, actionAll, actionUpdate, loading } = useShowAll()
    const rules = {
      shopify_private_app_api_key: {
        required
      },
      shopify_private_app_passsword: {
        required
      },
      shopify_base_url: {
        required
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    const update = async (callback=()=>{}) => {
      const isValid = validate()
      if( !isValid ) return
      const payload = Object.assign({}, {
        shopify_private_app_api_key: data.shopify_private_app_api_key,
        shopify_private_app_passsword: data.shopify_private_app_passsword,
        shopify_base_url: data.shopify_base_url
      })
      await actionUpdate(payload, (response) => {
        if( response.toLowerCase() === 'connection failed' ) return ElMessage({
          type: 'warning',
          message: 'Invalid Shopify configurations'
        })
        ElMessage({
          type: 'success',
          message: 'Shopify integration updated!'
        })
        isFunction(callback) && callback()
      })
    }

    await Promise.all([
      actionAll()
    ])

    return {
      state,
      data,
      loading,
      validation,
      update
    }
  }
})
</script>